import React from 'react';
import { useState } from 'react';
import Navbar from './Navbar';
import Sidebar from './Sidebar';
import {
  AiOutlinePlusCircle,
  AiOutlineMinusCircle,
  AiOutlineClose,
} from 'react-icons/ai';
import { GiSmartphone } from 'react-icons/gi';
import QRCode from 'qrcode.react';
import logoqr from '../../assets/images/logoqr.jpg';
import html2canvas from 'html2canvas';
import { jsPDF } from 'jspdf';
function ManageTableQr(props) {
  const printDocument = async () => {
    const pdf = new jsPDF('p', 'pt', 'a4');
    let x = 0;
    let y = 0;
    for (let i = 0; i < options.length; i++) {
      const input = document.getElementById('qr' + i);
      const canvas = await html2canvas(input);
      const imgData = canvas.toDataURL('image/png');
      pdf.addImage(imgData, 'JPEG', 80 + x * 150, 20 + y * 200);
      x++;
      if ((i + 1) % 3 === 0) {
        y++;
        x = 0;
      }
      if (y === 4) {
        pdf.addPage();
        x = 0;
        y = 0;
      }
    }
    pdf.save('download.pdf');
  };
  //store list
  const [storeList, setStoreList] = useState([
    {
      id: '1',
      name: 'TigerPuff',
    },
    {
      id: '2',
      name: 'McDonalds',
    },
    {
      id: '3',
      name: 'Jollibee',
    },
    {
      id: '4',
      name: 'KFC',
    },
  ]);
  const [storeSelect, setStoreSelect] = useState(null);
  const handleSetStoreSelect = (value) => {
    setQrDisplay(false);
    setStoreSelect(JSON.parse(value));
  };
  //
  const [options, setOptions] = useState([
    {
      id: '1',
      name: 'Table 1',
    },
    {
      id: '2',
      name: 'Table 2',
    },
    {
      id: '3',
      name: 'Table 3',
    },
    {
      id: '4',
      name: 'Table 4',
    },
    {
      id: '5',
      name: 'Table 5',
    },
  ]);
  //
  const [qrDisplay, setQrDisplay] = useState(false);
  const handleGenerate = () => {
    if (storeSelect !== null) {
      setQrDisplay(true);
    }
  };
  const [qrReview, setQrReview] = useState(null);
  return (
    <div className='wrapper'>
      <Sidebar />
      <div className='content'>
        <Navbar />
        <div className='container-fluid mainview-container'>
          <div className='row d-flex align-items-center'>
            <div className='col-lg-5 mb-2'>
              <h2 className='mb-1'>Order</h2>
              <div style={{ color: '#828282' }}>
                Home/ Order/ Manage Table QR
              </div>
            </div>
          </div>
          <div className='row'>
            <div className='col-lg-5'>
              <div className='card'>
                <div className='card-header fw-bold'>Generate QR for Table</div>
                <div className='card-body'>
                  <div className='mb-3'>
                    <label className='form-label'>Select Store Location</label>
                    <select
                      className='form-select'
                      id='inputGroupSelect02'
                      onChange={(e) => handleSetStoreSelect(e.target.value)}
                    >
                      <option selected disabled>
                        Choose...
                      </option>
                      {storeList.map((item) => {
                        return (
                          <option value={JSON.stringify(item)}>
                            {item.name}
                          </option>
                        );
                      })}
                    </select>
                  </div>
                  <div className='mb-3'>
                    <div>Add Table</div>
                    <p style={{ fontSize: '14px' }}>
                      Enter each QR code on a new line (or separated by one of
                      the supported characters below).
                    </p>
                  </div>
                  <div className='mb-4'>
                    <label className='form-label'>Options</label>
                    <div>
                      <ul
                        style={{ height: '200px', overflow: 'auto' }}
                        className='list-group border p-0 list-striped'
                      >
                        {options.map((item, index) => {
                          return (
                            <li className='list-group-item d-flex justify-content-between pe-1'>
                              <span>{item.name}</span>
                              <button
                                style={{ background: 'none' }}
                                className='border-0'
                              >
                                <AiOutlinePlusCircle />
                              </button>
                            </li>
                          );
                        })}
                      </ul>
                    </div>
                  </div>
                  <div className='row'>
                    <div className='col-md-5 offset-md-7 mb-2'>
                      <button
                        type='button'
                        className='form-control btn-outline-dark'
                        onClick={handleGenerate}
                      >
                        Generate
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className='col-lg-7'>
              <div className='card h-100'>
                <div className='card-header fw-bold'>Generated QR Code</div>
                <div
                  id='divToPrint'
                  style={{ maxHeight: '430px', overflow: 'auto' }}
                  className='card-body'
                >
                  <div className='row'>
                    {qrDisplay &&
                      options.map((item, index) => {
                        return (
                          <div className='col-lg-4 col-md-6 mb-2 d-flex align-items-center justify-content-center'>
                            <div
                              id={'qr' + index}
                              type='button'
                              className='d-flex flex-column align-items-center'
                              data-bs-toggle='modal'
                              data-bs-target='#staticBackdrop'
                              onClick={() =>
                                setQrReview({
                                  store_id: storeSelect?.id,
                                  store_name: storeSelect?.name,
                                  table_id: item?.id,
                                  table_name: item?.name,
                                })
                              }
                            >
                              <div className='qr-container'>
                                <div className='qr-title'>
                                  <span>Scan me</span>
                                  <GiSmartphone />
                                </div>
                                <div className='qr-body'>
                                  <QRCode
                                    size={90}
                                    value={JSON.stringify({
                                      store_id: storeSelect?.id,
                                      store_name: storeSelect?.name,
                                      table_id: item.id,
                                      table_name: item.name,
                                    })}
                                    fgColor='#033F7A'
                                  />
                                </div>
                              </div>
                              <div>{item.name}</div>
                            </div>
                          </div>
                        );
                      })}
                  </div>
                </div>
                <div className='row px-3'>
                  <div className='col-md-4 offset-md-8 mb-2'>
                    <button
                      type='button'
                      className='form-control btn-outline-dark'
                      onClick={printDocument}
                    >
                      Save as PDF
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          className='modal fade'
          id='staticBackdrop'
          data-bs-backdrop='static'
          data-bs-keyboard='false'
          tabIndex='-1'
          aria-labelledby='staticBackdropLabel'
          aria-hidden='true'
        >
          <div className='modal-dialog modal-dialog-centered modal-width'>
            <div className='modal-content'>
              <div className='modal-header border-0 p-1 d-flex justify-content-end'>
                <button
                  style={{ background: 'none' }}
                  className='close border-0'
                  data-bs-dismiss='modal'
                  aria-label='Close'
                >
                  <AiOutlineClose />
                </button>
              </div>
              <div class='modal-body'>
                <div className='d-flex flex-column align-items-center'>
                  <img
                    style={{ width: 200, height: 60 }}
                    className='mb-2'
                    src={logoqr}
                    alt=''
                  />
                  <div className='qr-container review'>
                    <div className='qr-title review'>
                      <span>Scan me</span>
                      <GiSmartphone />
                    </div>
                    <div className='qr-body review'>
                      <QRCode
                        size={150}
                        value={JSON.stringify(qrReview)}
                        fgColor='#033F7A'
                      />
                    </div>
                  </div>
                  <h4 className='fw-bold'>{qrReview?.table_name}</h4>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ManageTableQr;
